.notification {
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  padding-left: 20px;
  margin-bottom: 20px;
}

.error {
  background-color: lightpink;
}

.success {
  background-color: lightgreen;
}

@-webkit-keyframes fade {
  0% {opacity: 0}
  25% {opacity: 1}
  75% {opacity: 1}
  100% {opacity: 0}
}

@keyframes fade {
  0% {opacity: 0}
  25% {opacity: 1}
  75% {opacity: 1}
  100% {opacity: 0}
}

.error, .success {
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}



